import Bmusic from '../../components/bmusic.vue'
import Mheader from '../../components/header.vue'
import Hot from '../../components/hot.vue'
import Recommend from '../../components/recommend.vue'
import Search from '../../components/search.vue'
import My from '../../components/my.vue'
export default {
  data() {
    return {
      current: 'now',
      songList: [],
      imgurl: null,
      tablist: [{
          title: '正在热播',
          type: 'now'
        },
        {
          title: '推荐',
          type: 'recommend'
        },
        {
          title: '搜索',
          type: 'search'
        },
        {
          title: '我听过的',
          type: 'already'
        }
      ]
    }
  },
  computed: {
    // 获取vuex的bg图 用于防闪动
    getimgbg() {
      const url = this.$store.state.bgimgurl
      return url
    }
  },
  onLoad() {
    uni.showLoading({
      title: '正在加载请稍后。。。'
    })
    uni.request({
      url: 'http://39.107.80.8:5000/top/list?idx=1',
      method: 'GET',
      data: {},
      success: res => {
        // 截取100条
        const data = res.data.playlist.tracks.splice(0, 100)
        this.songList = data.map(item => {
          let sin = ''
          item.ar.forEach((itm, idx) => {
            if (idx == 0) {
              sin = itm.name
            } else {
              sin = sin + '/' + itm.name
            }
          })
          return {
            name: item.name,
            singer: item.ar.length == 1 ? item.ar[0].name : sin,
            id: item.id,
            picurl: item.al.picUrl
          }
        })
        uni.hideLoading()
        this.$store.commit('getsonglist', this.songList)
      },
      fail: () => {},
      complete: () => {}
    });
  },
  methods: {
    switchPage(val) {
      this.current = val
    },
    // 等图片加载完毕后再进行加载背景图防止图片是一点一点加载的
    onImageLoad(ev) {
      const url = this.$store.state.bgimgurl
      
      this.$store.commit('changeAvatar', url)
      
      this.imgurl = `url(${url})`
    }
  },
  components: {
    Bmusic,
    Mheader,
    Hot,
    Recommend,
    Search,
    My
  }
}
